
const ClientsList = ({clients : any, reverse}) => {
    const tableClients = clients.map((e:any) => (
      <tr key={e._id}>
        <td>{e.dateAdded}</td>
        <td>{e.name}</td>
        <td>{e.age}</td>
        <td>{e.address}</td>
        <td>
          <Link href="/clients/[id]" as={`/clients/${e._id}`}>
            <a>Go to client</a>
          </Link>
        </td>
      </tr>
    ));
    return reverse ? tableClients.reverse() : tableClients;
  };

  export default ClientsList;